require('./assets/css/font-awesome.less')
import Vue from 'vue'
import  VueRouter from 'vue-router'
import App from './components/App.vue'
import City from "./components/city.vue"
import Resource from "vue-resource"
// 
var Foo = Vue.extend({
    template: '<p>This is foo!</p>'
})

var Bar = Vue.extend({
    template: '<p>This is bar!</p>'
})
Vue.use(Resource);
Vue.use(VueRouter)
// 
//  HTML 
// var App = Vue.extend({})

// 
// 
var router = new VueRouter()

// 
//  Vue.extend
// 
// 
router.map({
    '/foo': {
        component: Foo
    },
    '/bar': {
        component: Bar
    }
})

// 
//  App  #app 
router.start(City, '#app')


